.solar_systm {
    transform-style: preserve-3d;
    pointer-events: none;
    height: 100%;
    position: relative; /* absolute에서 relative로 변경하여 부모 요소에 종속되게 만듦 */
    left: 0;
    right: 0;
}

* {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.solar {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black; /* 배경색 추가하여 우주 느낌 강조 */
}

.menu {
    color: white;
    display: block;
    position: relative;
    cursor: pointer;
    margin: 10px; /* 여백 추가 */
    z-index: 2;
}

.preview {
    width: 50px; /* 미리보기 크기 증가 */
    height: 50px; /* 미리보기 크기 증가 */
    background: yellow;
    background-size: auto 100% !important;
    border-radius: 50%; /* 둥근 모양 유지 */
    position: relative;
    margin-bottom: 5px; /* 텍스트와 미리보기 사이에 약간의 여백 추가 */
}

.info {
    position: relative;
    text-align: center; /* 텍스트를 중앙 정렬 */
}

h2, h3 {
    text-transform: uppercase;
    margin: 0;
    font-weight: 100;
    letter-spacing: 2px;
    font-size: 14px; /* 폰트 크기 증가 */
}

h2 {
    font-size: 16px;
    opacity: 0.8; /* 글자 가독성 증가 */
    margin-bottom: 4px;
}

h3 {
    font-size: 12px;
    opacity: 0.6;
}

.pip {
    width: 10px;
    height: 10px;
    background: #fea082;
    float: left;
    position: relative;
    top: 3px;
    margin-right: 5px;
    border-radius: 50%;
}

.planet_description {
    text-align: center;
    color: white;
    margin-top: 20px;
}

.planet_description h1 {
    font-size: 24px;
    margin-bottom: 10px;
}

.planet_description h2 {
    font-size: 16px;
    margin-bottom: 5px;
}

.planet_description p {
    font-size: 14px;
    line-height: 1.5;
}

.planet9, .planet8, .planet7, .planet6, .planet5, .planet4, .planet3, .planet2, .planet1 {
    appearance: none;
    position: relative;
    width: 20px;
    height: 20px;
    border: 2px solid white;
    border-radius: 50%;
    margin: 5px;
    outline: none;
    z-index: 3;
}